import React from 'react';
import './ProductColorView.css';

const ProductColorView = ({color}) => {

  const multiColor = color === "разноцветный" ? 'multi-colored' : 'product-color-view';

  let selectedColor;
  switch (color) {
    case "черный":
      selectedColor = "black";
      break;
    case "белый":
      selectedColor = "white";
      break;
    case "синий":
      selectedColor = "blue";
      break;
    default:
      selectedColor = "red"
  }

  const divStyle = {
    backgroundColor: `${selectedColor}`
  };




  return (
    <div style={divStyle}
         className={multiColor}>
    </div>
  )
};

export default ProductColorView;
